	module.exports = {"data":{"site":{"siteMetadata":{"title":"Dan Beaven's blog","author":"Dan Beaven"}},"markdownRemark":{"id":"/Users/dan/Code/blog/src/pages/posts/05-why-interaction-design.md absPath of file >>> MarkdownRemark","html":"<h1>Why interaction design?</h1>\n<p>With a focus on interaction design in this week’s lecture we discussed the various forms that design takes. We touched on experience design and immersive design two disciplines whose methods closely overlap those of interaction design. We also talked more broadly about the different disciplines that come under design which are neatly illustrated in Thomas Pluralvonglas (2009) venn diagram on the right. In this essay I explore further what interaction design means, what sort of problems it is there to solve and touch on what it means to my own work.<img src=\"https://lh3.googleusercontent.com/wWb8VtJnrY7Zv0ceGZNBYFhB0I5OXmRul89xrIR6cAo8E_PpOvk8hyR6nv0DCbVzqDOIUm_JHoQPupfbu5UM8GB8iHR8eVZoGVX51tPx0ZYGChzBekGyTTd9RelZU9C5myPuH9CR\" alt=\"img\"></p>\n<p>Computers are only becoming more ubiquitous and they have been at an accelerated rate since the beginning of their conception (Sade 2006). Due in part to the rate at which they are developing and also the fact that they tend not to be built by designers eminently focused on the end user but by engineers trying to solve technical problems. I still see many especially in the older generations who struggle to understand and use computers. Or atleast it is hard for them to see the benefits when the time and energy investment for them to learn these systems is so large. Interactions design questions usability it inspects our behaviour and analyzes the choices we make (Kolko, 2009). This is why I believe it can play an important role in not only making technology more accessible but question the way we use it.</p>\n<p>In the studio I am very conscious of this idea of questioning the way we use and interact with objects and technology. With this in mind in response to my latest brief on visualizing time I am looking at new ways of interacting with a book. I am trying to use interactivity as a tool to teach technical abstract ideas.</p>\n<h4>References</h4>\n<p>Kolko, D.J. (2009) Thoughts on interaction design. Morgan Kaufmann Publishers.</p>\n<p>Sade, G. (2006) ‘Why interaction design? Review of Jonas Löwgren and Erik Stolterman Thoughtful interaction Design’, Design Philosophy Papers, 4(1), pp. 41–52. doi: 10.2752/144871306x13966268131352.</p>\n<p>Pluralvonglas, T. (2009) The Disciplines of User Experience Design. [Infographic].\nAvailable at <a href=\"https://www.google.com/url?q=http://bit.ly/NZ3dxo&#x26;sa=D&#x26;ust=1517590438654000&#x26;usg=AFQjCNFsYlL_K-rtEDxX1XGHtoO7Wpp0rw\">http://bit.ly/NZ3dxo</a>. Accessed 20th October 2016</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-json\"><code class=\"language-json\"><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">\"date\"</span><span class=\"token operator\">:</span> <span class=\"token string\">\"17/11/2016\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"description\"</span><span class=\"token operator\">:</span>\n    <span class=\"token string\">\"Computers are only becoming more ubiquitous and they have been at an accelerated rate since the beginning of their conception\"</span><span class=\"token punctuation\">,</span>\n  <span class=\"token property\">\"images\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token property\">\"thumbnail\"</span><span class=\"token operator\">:</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token property\">\"src\"</span><span class=\"token operator\">:</span>\n        <span class=\"token string\">\"https://lh3.googleusercontent.com/wWb8VtJnrY7Zv0ceGZNBYFhB0I5OXmRul89xrIR6cAo8E_PpOvk8hyR6nv0DCbVzqDOIUm_JHoQPupfbu5UM8GB8iHR8eVZoGVX51tPx0ZYGChzBekGyTTd9RelZU9C5myPuH9CR\"</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>","frontmatter":{"title":"Why interaction design?","date":"November 17, 2016"}}},"pathContext":{"slug":"/posts/05-why-interaction-design/","previous":{"fields":{"slug":"/posts/04-research-methodologies/"},"frontmatter":{"title":"Research methodologies","description":"A large part of the researcher's job is analysing their own input, being aware of their own role in what they are studying.","thumbnail":null,"gallery":null,"essay":null}},"next":{"fields":{"slug":"/posts/06-measuring-the-impact-of-culture-on-the-economy/"},"frontmatter":{"title":"Measuring the impact of culture on the economy","description":"Each decision to fund one cause is also the decision not to fund another.","thumbnail":null,"gallery":null,"essay":null}}}}

